	import WxValidate from '../../../utils/WxValidate.js';
	import mpopup from "../../../components/xuan-popup/xuan-popup.vue"
	import {
		ADDCONTACT
	} from "../../../utils/api.js"
	export default {
		components: {
			mpopup
		},
		data() {
			return {
				name: "",
				mobile: "",
				addShow: "",
				isdisable: false

			}
		},
		onLoad(options) {
			this.mobile = options.mobile
			this.initValidate();
		},
		methods: {
			// 规则校验
			initValidate() {
				// 规则
				const rules = {
					nickName: {
						required: true
					},
					mobile: {
						required: true,
						tel:true
					}
				}
				// 提示
				const messages = {
					nickName: {
						required: "请输入昵称！"
					},
					mobile: {
						required: "请输入手机号"
					}
				}
				this.WxValidate = new WxValidate(rules, messages)
			},
			// 添加联系人列表
			addContact(e) {
				this.isdisable = true
				var _this =this
				setTimeout(function(){
					_this.isdisable = false
					_this.$forceUpdate()
				},1000)
				const params = e.detail.value
					if (!this.WxValidate.checkForm(params)) {
						const error = this.WxValidate.errorList[0]
						// 可以自定义提示
						uni.showToast({
							title: `${error.msg}`,
							icon: 'none'
						});
						return false
					} else {	
						const params = {
								name: this.name,
								mobile: this.mobile
							}
							this.$request.url_request(ADDCONTACT, params, "GET", res => {
								
								console.log(JSON.parse(res.data).code)
								if(JSON.parse(res.data).code == 10000530){
									uni.showToast({
										title:JSON.parse(res.data).msg,
										icon:"none",
										duration:1000
									})
								}else{
										uni.showToast({
									title: "添加成功!",
									icon: "success",
									mask: true,
									duration: 1000
								})
								
								setTimeout(function() {
									uni.hideToast()
									uni.navigateBack({
										
									})
								}, 1000)
								}
								
							
							}, err => {})
						
					}
				}
	}
 }
